@charset "UTF-8";
html,
body {
  font-family: Arial,"Hiragino Sans GB","\51AC\9752\9ED1","Microsoft YaHei","\5FAE\8F6F\96C5\9ED1",SimSun,"\5B8B\4F53",Helvetica,Tahoma,Arial sans-serif;
  font-size: 16px;
  line-height: 1.6;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  text-align: left; }

* {
  padding: 0;
  margin: 0; }

a {
  text-decoration: none;
  color: #333; }

a:focus,
input:focus {
  outline: none; }

ul,
li,
p,
h1,
h2,
h3 {
  margin: 0;
  padding: 0; }

ul,
li {
  list-style: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal; }

.container {
  width: 1020px;
  padding: 0 20px;
  margin: 0 auto; }

*, ::after, ::before {
  box-sizing: border-box; }

.doc {
  width: 1020px;
  padding: 20px;
  margin: 0 auto; }
  .doc img {
    vertical-align: middle;
    max-width: calc(100% - 50px);
    border: 0;
    border-radius: 4px;
    margin: 0 25px;
    box-shadow: 0px 8px 16px #999999; }
  .doc h1, .doc h2, .doc h3, .doc h4 {
    color: #111111;
    font-weight: 400;
    margin-top: 1em; }
    .doc h1 a, .doc h2 a, .doc h3 a, .doc h4 a {
      display: none; }
  .doc h1, .doc h2, .doc h3, .doc h4, .doc h5, .doc dl {
    margin-bottom: 16px;
    padding: 0; }
  .doc p {
    margin-top: 8px;
    margin-bottom: 3px; }
  .doc h1 {
    font-size: 48px;
    line-height: 54px; }
  .doc h2 {
    font-size: 36px;
    line-height: 42px; }
  .doc h1, .doc h2 {
    border-bottom: 1px solid #EFEAEA;
    padding-bottom: 10px; }
  .doc h3 {
    font-size: 24px;
    line-height: 30px; }
  .doc h4 {
    font-size: 21px;
    line-height: 26px; }
  .doc h5 {
    font-size: 18px;
    line-height: 23px; }
  .doc a {
    color: #0099ff;
    margin: 0 2px;
    padding: 0;
    vertical-align: baseline;
    text-decoration: none; }
  .doc a:hover {
    text-decoration: none;
    color: #ff6600; }
  .doc a:visited {
    /*color: purple;*/ }
  .doc ul, .doc ol {
    padding: 0;
    padding-left: 18px;
    margin: 0; }
  .doc ol li {
    list-style-type: inherit; }
  .doc ul li {
    list-style-type: disc; }
  .doc li {
    line-height: 24px; }
  .doc p, .doc ul, .doc ol {
    font-size: 16px;
    line-height: 24px; }
  .doc ol ol, .doc ul ol {
    list-style-type: lower-roman; }
  .doc code, .doc pre {
    font-family: Consolas, Monaco, Andale Mono, monospace;
    background-color: #f7f7f7;
    color: inherit; }
  .doc code {
    font-family: Menlo, Monaco, Consolas, "Courier New", monospace !important;
    line-height: 1.4;
    color: #666666;
    background-color: #ebf4fe;
    border-radius: 3px;
    padding: 1px 4px;
    font-size: 14px; }
  .doc pre {
    padding: 1.2em !important;
    margin: 1.2em 0 !important;
    border-radius: 5px;
    background: #282c34;
    overflow: auto; }
    .doc pre code {
      color: #b8b8b8;
      background-color: transparent;
      padding-left: 0; }
  .doc code {
    color: #666555; }
  .doc aside {
    display: block;
    float: right;
    width: 390px; }
  .doc blockquote {
    border-left: .5em solid #eee;
    padding: 0 0 0 2em;
    margin-left: 0; }
  .doc blockquote cite {
    font-size: 14px;
    line-height: 20px;
    color: #bfbfbf; }
  .doc blockquote cite:before {
    content: '\2014   \A0'; }
  .doc blockquote p {
    color: #666; }
  .doc hr {
    text-align: left;
    color: #999;
    height: 2px;
    padding: 0;
    margin: 16px 0;
    background-color: #e7e7e7;
    border: 0 none; }
  .doc dl {
    padding: 0; }
  .doc dl dt {
    padding: 10px 0;
    margin-top: 16px;
    font-size: 1em;
    font-style: italic;
    font-weight: bold; }
  .doc dl dd {
    padding: 0 16px;
    margin-bottom: 16px; }
  .doc dd {
    margin-left: 0; }
  .doc table {
    *border-collapse: collapse;
    /* IE7 and lower */
    border-spacing: 0;
    width: 100%; }
  .doc table {
    border: solid #ccc 1px; }
  .doc table thead {
    background: #f7f7f7; }
  .doc table thead tr:hover {
    background: #f7f7f7; }
  .doc table tr:hover {
    background: #fbf8e9;
    -o-transition: all 0.1s ease-in-out;
    -webkit-transition: all 0.1s ease-in-out;
    -moz-transition: all 0.1s ease-in-out;
    -ms-transition: all 0.1s ease-in-out;
    transition: all 0.1s ease-in-out; }
  .doc table td, .doc .table th {
    border-left: 1px solid #ccc;
    border-top: 1px solid #ccc;
    padding: 10px;
    text-align: left; }
  .doc table th {
    border-top: none;
    text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
    padding: 5px;
    border-left: 1px solid #ccc; }
  .doc table td:first-child, .doc table th:first-child {
    border-left: none; }
